import React from 'react'

export const FormItem = ({ props }) => {
  const type = props.type ? props.type : 'text'
  const name = props.name ? props.name : props.id
  return (
    <div>
      <label htmlFor={props.id} />
      <input type={type} id={props.id} name={name} />
    </div>
  )
}
